// color
$classic-red: #74151f;

@import "normalize";
@import "common";
@import "layout";
@import "background-color";
@import "header";
@import "footer";
@import "index";
@import "lity.min";
@import "slick";
@import "slick-theme";
@import "details";
@import "exhibitions";
@import "exhibitions";
@import "gallery";
@import "document-page";

#wrap.sc, #wrap.tc, #wrap.ko {
  header.main-header nav ul {
    margin-left: 274px;
  }
}

#wrap.ja {
  header.main-header nav ul {
    margin-left: 210px;
  }
  .index__short-description__box_text p.ja-style-change {
    line-height: 2;
  }
  .index__blog-link__box_text h2 {
    margin-top: 4px;
  }
  .footer__links ul {
    width: 620px;
    li .js-langChild {
      right: -10px;
    }
  }
  .index__blog-link__box_text img.blog {
    margin: 50px 0 0 92px;
  }
  .index__news ul li .date {
    font-size: 14px;
    margin: 0 0 0 0;
  }
  .details__header .subtitle, .details__header h3 {
    font-family: "ヒラギノ明朝 ProN W6", "HiraMinProN-W6", "游明朝";
  }
  .index__short-description__box_text dl dd {
    margin-bottom: 1em;
  }
}
#wrap.en {
  .utl__cardbox.video {
    .utl__cardbox_text {
      padding-top: 34px;
      p {
        margin-top: 10px;
        font-size: 14px;
      }
    }
  }
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a, a > * {
  transition: 1s ease-in-out;
  &:hover {
    transition: .5s ease-in-out;
  }
}
