.exhibitions__header {
  padding: 20px 0 37px;
  .inner-container {
    width: 940px;
  }
  h2, .subtitle {
    text-align: center;
    @extend .serif;
    font-size: 22px;
    line-height: 27px;
  }
  h2 {
    margin: 26px 0 35px;
  }
  h3.subtitle {
    @extend .serif;
    font-size: 27px;
    line-height: 1.3em;
    color:#a17d46;
    text-align: center;
    border-top: 3px solid #000;
    border-bottom: 1px solid #000;
    padding: 12px 0 7px;
    margin-bottom: 0;
  }
  h3.center {
    @extend .serif;
    font-size: 27px;
    margin: 22px 0 0;
  }
}
.exhibitions__hero {}
.exhibitions__grid {
  @extend .clearfix;
  padding-bottom: 40px;
  .exhibitions__grid_half {
    float: left;
    width: 610px;
    margin-right: 20px;
    &:nth-child(2n) {
      margin-right: 0;
    }
  }
  p {
    font-size: 16px;
    line-height: 1.6;
    margin: 54px 0;
  }
  img {
    margin-bottom: 17px;
  }
}
