.document-page {
  padding: 20px 0;
  .inner-container {
    width: 940px;
  }
  h2 {
    margin: 24px -14px 56px;
    padding-bottom: 38px;
    text-align: center;
    border-bottom: 1px solid #000;
  }
  h3 {
    margin-bottom: 6px;
  }
  p {
    margin: 6px 0 36px;
    font-size: 16px;
    line-height: 26px;
  }
  a {
    color: $classic-red;
  }
  ul {
    list-style: none;
    font-size: 16px;
    line-height: 26px;
  }
}
