.utl__cardbox {
  height: 330px;
  @extend .clearfix;
  background: #FFF;
  margin-bottom: 40px;
  border-bottom: 20px solid $classic-red;
  box-shadow: 0 5px 10px rgba(81,70,65,.25);
  &_text {
    float: left;
    width: 440px;
    padding-top: 80px;
    margin: 0 90px 0;
    h3 {
      @extend .serif;
      font-size: 40px;
      margin: 0;
      text-align: center;
    }
    p {
      margin-top: 24px;
      font-size: 16px;
      line-height: 22px;
    }
    dl {
      @extend .mp0;
      @extend .clearfix;
      padding-left: 14px;
      font-size: 16px;
      line-height: 1.4em;
      dt {
        width: 100px;
        float: left;
      }
      dd {
        @extend .mp0;
        width: 320px;
        float: left;
      }
    }
    a.box {
      @extend .none-href;
      margin: 50px auto 0;
      display: block;
      color: #000;
      border: 2px solid #000;
      font-size: 18px;
      width: 250px;
      height: 50px;
      text-align: center;
      padding-top: 12px;
      &:hover {
        background: #000;
        color: #fff;
      }
    }
  }
  &_img {
    float: left;
  }
}

.details__header {
  padding: 20px 0 56px;
  .inner-container {
    width: 940px;
  }
  h2, .subtitle {
    text-align: center;
    @extend .serif;
    font-size: 22px;
    line-height: 27px;
  }
  h2 {
    margin: 26px 0 6px;
  }
  .subtitle {
    margin: 0 0 41px;
  }
  h3 {
    @extend .serif;
    font-size: 27px;
    line-height: 1;
    color:#a17d46;
    text-align: center;
    border-top: 3px solid #000;
    border-bottom: 1px solid #000;
    padding: 18px 0 12px;
    margin-bottom: 30px;
  }
  p {
    font-size: 16px;
    line-height: 26px;
    margin-bottom: 30px;
  }
  table {
    background: #d4d3d1;
    .title {
      width: 258px;
    }
    td {
      padding: 14px 20px ;
      border: 2px solid #fff;
      font-size: 16px;
      &.area {
        h4 {
          font-size: 16px;
          line-height: 1.4;
          font-weight: normal;
          margin: 0 0 10px 0;
        }
        img {
          float: left;
          margin-right: 20px;
        }
        p {
          font-size: 14px;
          line-height: 1.3;
          margin: 0;
        }
      }
    }
  }
}
.details__category {
  padding-bottom: 20px;
  h2 {
    @extend .mp0;
    height: 150px;
    background: #282828;
    img {
      display:block;
      margin: 0 auto;
      padding-top: 60px;
    }
  }
  .inner-container {
    margin: 60px auto 0;
  }
}
.details__enter {
  padding-top: 57px;
  padding-bottom: 60px;
  .inner-container {
    width: 940px;
  }
  table {
    background: #fff;
    .title {
      width: 180px;
    }
    td {
      padding: 13px 20px ;
      border: 3px solid #D8D7D5;
      font-size: 16px;
      line-height: 1.6;
    }
  }
  a {
    @extend .none-href;
    margin: 60px auto 0;
    display: block;
    color: #fff;
    background: #282828;
    font-size: 18px;
    line-height: 60px;
    width: 300px;
    height: 60px;
    text-align: center;
    &:hover {
      background: #000;
      color: #fff;
    }
  }
}
.details__museum {
  height: 400px;
  max-width: 1920px;
  margin: 0 auto;
  overflow: hidden;
  position: relative;
  h2 {
    @extend .mp0;
    width: 953px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -55px;
    margin-left: -476px;
  }
  >img {
    width: 100%;
    min-width: 1240px;
  }
}
.details__guide {
  padding: 20px 0 24px;
  .inner-container {
    width: 940px;
  }
  .document-style {
    p {
      font-size: 16px;
      line-height: 23px;
      margin-bottom: 2em;
    }
    h2 {
      font-size: 50px;
      text-align: center;
      margin-bottom: -12px;
      line-height: 1;
    }
    h3 {
      font-size: 26px;
      text-align: center;
      font-weight: normal;
      margin: 1.4em 0;
      &.contrast {
        background: #FFF;
        padding: 10px 0;
      }
    }
  }
}
