.index__hero {
  width: 100%;
  min-height: 499px;
  max-height: 773px;
  max-width: 1920px;
  margin: 0 auto 159px;
  position: relative;
  .index__hero__text {
    position: absolute;
    z-index: 9999;
    width: 953px;
    top: 50%;
    left: 50%;
    margin-top: -50px;
    margin-left: -476px;
    .hero__text__box {
      height: 110px;
    }
    a {
      @extend .none-href;
      margin: 84px auto 0;
      display: block;
      color: #fff;
      background: rgba(0,0,0,.15);
      border: 2px solid #fff;
      font-size: 18px;
      line-height: 46px;
      width: 250px;
      height: 50px;
      text-align: center;
      p {
        @extend .mp0;
      }
      &:hover {
        background: #FFF;
        color: #7c6a64;
      }
    }
  }
  .index__hero__images {
    overflow: hidden;
    ul {
      @extend .mp0;
    }
    img {
      @extend .mp0;
      width: 100%;
      min-width: 1240px;
      vertical-align: bottom;
    }
  }
  .index__hero__intro {
    position: absolute;
    bottom: -159px;
    left: 0;
    width: 100%;
    height: 159px;
    padding-top: 43px;
    text-align: center;
  }
}

.index__short-description {
  padding-top: 60px;
  padding-bottom: 20px;
  &__box {
    @extend .clearfix;
    background: #FFF;
    height: 640px;
    margin-bottom: 40px;
    border-bottom: 20px solid $classic-red;
    box-shadow: 0 5px 10px rgba(81,70,65,.25);
    &_text {
      float: left;
      width: 460px;
      padding-top: 110px;
      margin: 0 80px 0;
      h2 {
        font-size: 40px;
        margin: 0;
        text-align: center;
      }
      p {
        margin-top: 24px;
        font-size: 16px;
        line-height: 22px;
      }
      dl {
        @extend .mp0;
        @extend .clearfix;
        padding-left: 14px;
        font-size: 16px;
        line-height: 1.4em;
        dt {
          width: 100px;
          float: left;
        }
        dd {
          @extend .mp0;
          width: 320px;
          float: left;
        }
      }
      a.box {
        @extend .none-href;
        margin: 50px auto 0;
        display: block;
        color: #000;
        border: 2px solid #000;
        font-size: 18px;
        line-height: 46px;
        width: 250px;
        height: 50px;
        text-align: center;
        &:hover {
          background: #000;
          color: #fff;
        }
      }
    }
    &_img {
      float: left;
    }
  }
}
.index__movie {
  height: 400px;
  max-width: 1920px;
  margin: 0 auto;
  overflow: hidden;
  position: relative;
  .button {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -72px;
    margin-left: -50px;
    &:hover {
      opacity: .5;
    }
  }
  >img {
    width: 100%;
    min-width: 1240px;
  }
}

.index__blog-link {
  padding: 60px 0 40px 0;
  &__box {
    @extend .clearfix;
    display: block;
    margin: 0 140px 20px;
    background: #FFF;
    &_text {
      float: left;
      width: 480px;
      height: 200px;
      background: #000;
      color: #fff;
      padding: 20px 40px 0;
      h2 {
        font-size: 27px;
        text-align: center;
        margin-bottom: 0;
      }
      p {
        font-weight: 200;
        margin: 6px 0 0;
      }
      img.blog {
        margin: 64px 0 0 20px;
      }
    }
    &_img {
      float: left;
      width: 480px;
      height: 200px;
    }
    &.hover-a:hover {
      .index__blog-link__box_text {
        background: #3a589e;
      }
      .index__blog-link__box_img {
        opacity: .7;
      }
    }
    &.hover-b:hover {
      .index__blog-link__box_text {
        background: #2e8d3c;
      }
      .index__blog-link__box_img {
        opacity: .7;
      }
    }
  }
}

.index__news {
  padding: 60px 0 40px 0;
  .inner-container {
    width: 960px;
    position: relative;
  }
  h2 {
    @extend .mp0;
    font-size: 40px;
    text-align: center;
    line-height: 1;
  }
  ul {
    @extend .none-list;
    @extend .mp0;
    margin-top: 20px;
    li {
      @extend .clearfix;
      margin: 0 0 20px;
      a {
        @extend .none-href;
        color: #000;
        &:hover {
          color: #ff8a1b;
        }
      }
      img {
        float: left;
        margin-right: 22px;
      }
      .date {
        display: inline-block;
        font-size: 16px;
        margin: 0 0 8px 0;
      }
      h3 {
        @extend .mp0;
        display: inline-block;
        font-weight: normal;
        text-decoration: underline;
      }
    }
  }
  &_link {
    @extend .clearfix;
    @extend .none-href;
    color: #595757;
    position: absolute;
    top: 0;
    right: 0;
    width: 80px;
    img {
      float: left;
      margin-right: 10px;
    }
    p {
      line-height: 1.6em;
      @extend .mp0;
    }
    &:hover {
      color: #ff8a1b;
    }
  }
}
