.clearfix{zoom: 1;}
.clearfix:after{
    content: "";
    display: block;
  clear: both;
}

*, *:before, *:after {
   box-sizing: border-box;
}

.hide-text {
text-indent: 100%;
white-space: nowrap;
overflow: hidden;
}

html {
  font-family: 'Helvetica Neue',"Hiragino Kaku Gothic ProN" "游ゴシック", YuGothic, Meiryo, sans-serif;
  font-size: 14px;
}

@import url(https://fonts.googleapis.com/css?family=Cinzel+Decorative:400);
h2, .serif {
  font-family: 'Cinzel Decorative', cursive, "ヒラギノ明朝 ProN W6", "HiraMinProN-W6", "游明朝";
}

.mp0 {
  margin: 0 0 0 0;
  padding: 0 0 0 0;
}
.none-href {
  text-decoration: none;
}
.none-list {
  list-style: none;
}
.center {
  text-align: center;
}
