// background color
html {
  background:#000;
}
header.main-header {
  background: #000;
  border-bottom: 40px solid $classic-red;
}
footer.main-footer {
  background: #000;
}

// index
.index__hero {
  .index__hero__intro {
    background: #CDCAC5;
  }
}
.index__short-description {
  background: #f3f3f3;
}
.index__blog-link {
  background: #dad9d7;
}
.index__news {
  background: #F3F3F3;
}

// contest-details
.details__header {
  background: #FFF;
}
.details__category {
  background: #F3F3F3;
}
.details__enter, .details__guide {
  background: #D8D7D5;
}

// exhibitions
.exhibitions {
  background: #FFF;
}

// document pages
.document-page {
  background: #FFF;
}
