// header
header.main-header {
  .inner-container {
    height: 87px;
    >a {
      display: block;
      height: 80px;
      width: 819px;
      margin: 33px auto 7px;
      h1, img {
        @extend .mp0;
      }
    }
  }
  nav {
    @extend .mp0;
    display: block;
    height: 40px;
    ul {
      @extend .mp0;
      @extend .clearfix;
      margin-left: 150px;
      li {
        @extend .mp0;
        @extend .none-list;
        float: left;
        a {
          @extend .none-href;
          display: block;
          color: #ffbe1b;
          line-height: 40px;
          font-size: 18px;
          margin-right: 92px;
          &.last {
            margin-right: 0;
          }
          &:hover {
            color: #ff8a1b;
          }
        }
      }
    }
  }
}
