.gallery {
  background: #FFF;
  padding-bottom: 60px;
  .exhibitions__header {
    padding: 20px 0 60px;
  }
}

.gallery__item {
  &__title {
    text-align: center;
    background: #282828;
    height: 100px;
    line-height: 100px;
    margin: 0 0 23px;
    padding: 0 0 0 0;
    h3 {
      @extend .mp0;
      @extend .serif;
      color: #fff;
      font-size: 28px;
    }
  }
}

.gallery__item__block {
  @extend .clearfix;
  margin: 0 auto;
  padding: 60px 0 26px;
  &_main {
    float: left;
    width: 900px;
    margin-right: 60px;
    &__img {
      background: #EEE;
      text-align: center;
      img {
        text-align: center;
        vertical-align: bottom;
        max-width: 900px;
        max-height: 900px;
      }
    }
    &__title {
      h4 {
        font-size: 24px;
        margin: 28px 0 20px;
      }
    }
    &__description {
      p {
        font-size: 16px;
        line-height: 26px;
        margin-bottom: 26px;
      }
    }
    &__site {
      ul {
        @extend .none-list;
        @extend .mp0;
        font-size: 16px;
        line-height: 1.8;
        a {
          color: #a51b24;
          text-decoration: none;
        }
      }
    }
    &__category {
      .btn {
        display:block;
        height: 40px;
        background: #282828;
        color: #fff;
        font-size: 20px;
        line-height: 40px;
        text-align: center;
        text-decoration: none;
        padding: 0 0 0 0;
        margin: 20px 0;
      }
      .thumnails a {
        margin: 0 20px 0 0;
        display: inline-block;
        &:last-child {
          margin-right: 0;
        }
        &:hover {
          opacity: 0.7;
        }
      }
      img {
        vertical-align: bottom;
        background: #FFF;
      }
    }
  }
  &_sub {
    float: left;
    width: 280px;
    &__name {
      h3 {
        font-size: 30px;
        margin: 24px 0 16px;
      }
    }
    p {
      font-size: 15px;
      line-height: 26px;
      margin-bottom: 24px;
    }
  }
}

.gallery-pages {
  .gallery__item__block.bg2n{
    background: #eee;
    margin: -24px auto 0;
    padding: 60px 0 26px;
    .gallery__item__block_main__img {
      background: #FFF;
    }
  }
}

// ほぼ共通
.gallery__honered__header {
  text-align: center;
  background: #282828;
  height: 60px;
  margin: 23px 0;
  padding: 0 0 0 0;
  h4 {
    @extend .mp0;
    color: #fff;
    font-weight: normal;
    text-transform: capitalize;
    font-size: 20px;
    line-height: 60px;
  }
}
