footer.main-footer {
  .footer__top {
    height: 99px;
    background: #FFF;
    a {
      display: block;
      margin: 0 auto;
      padding-top: 37px;
      width: 44px;
      height: 25px;
    }
  }
  .footer__sns {
    background:$classic-red;
    ul {
      @extend .clearfix;
      width: 270px;
      height: 80px;
      margin: 0 auto;
      li {
        @extend .mp0;
        @extend .none-list;
        float: left;
        a {
          @extend .none-href;
          display: block;
          height: 23px;
          margin-top: 29px;
          margin-right: 75px;
          position: relative;
          img {
            position: absolute;
            bottom: 0;
            left: 0;
            opacity: .6;
            &:hover {
              opacity: 1;
            }
          }
          &.last {
            margin-right: 0;
          }
        }
      }
    }
  }
  .footer__links {
    position: relative;
    background: #282828;
    height: 150px;
    ul {
      @extend .clearfix;
      position: relative;
      width: 600px;
      height: 170px;
      margin: 0 auto;
      li {
        @extend .mp0;
        @extend .none-list;
        float: left;
        a {
          @extend .none-href;
          color: #fff;
          font-weight: 200;
          display: block;
          height: 16px;
          margin-top: 32px;
          margin-right: 18px;
          padding-right: 18px;
          border-right: 1px solid #fff;
          &:hover {
            color: #ffb81b;
          }
          &.last {
            margin-right: 0;
            padding-right: 0;
            border-right: none;
          }
          small {
            font-size: 10px;
            margin-left: 15px;
          }
        }
        &.js-langSelector {
          &.on > a {
            color: #ffb81b !important;
          }
        }
        .js-langChild {
          display: none;
          position: absolute;
          bottom: 145px;
          right: 32px;
          &.on {
            display:block;
          }
          width: 180px !important;
          margin: 0 0 0 0;
          padding: 0 0 0 0;
          li {
            float: none;
            a {
              border-right: none;
              display: block;
              margin: 0 0 0 0;
              height: 34px;
              padding: 0 0 0 20px;
              font-size: 16px;
              line-height: 34px;
              background: #000;
              opacity: .9;
              &:hover {
                background: #444;
              }
              &.last:after {
                content: "▼";
                position: absolute;
                font-size: 20px;
                transform: scale(1, .7);
                right: 16px;
                bottom: -20px;
                color: #000;
                opacity: .9;
                transition: 1s ease-in-out;
              }
              &.last:hover:after {
                color: #444;
                transition: .5s ease-in-out;
              }
            }
          }
        }
      }
    }
    .ssl {
      position: absolute;
      top: 80px;
      left: 50%;
      margin-left: -62px;
      width: 125px;
      height: 45px;
      text-align: center;
    }
  }
  .footer__copyrights {
    height: 200px;
    p {
      @extend .mp0;
      padding: 50px 0 0 0;
      color: #fff;
      text-align: center;
      font-size: 13px;
      font-weight: 200;
    }
  }
}
